
import greatCircle from '@turf/great-circle';
import { point } from '@turf/helpers';
import { createStore } from 'vuex';

const checkup = (state) => {
  if (!state.fromCoords?.length || !state.toCoords?.length) return;

  const route = greatCircle(point(state.fromCoords), point(state.toCoords));

  state.map.getSource('routes').setData(route);
}

export const store = createStore({
  state() {
    return {
      map: null,
      fromCoords: [],
      toCoords: []
    }
  },
  getters: {
  },
  mutations: {
    initMap(state, map) {
      state.map = map;
    },
    setFrom(state, from) {
      state.fromCoords = from;
      checkup(state);
    },
    setTo(state, to) {
      state.toCoords = to;
      checkup(state);
    },
  }
})