<template>
  <div class="search">
    <v-autocomplete v-model="from" @update:model-value="updateFrom" :items="items" label="From" item-title="code"
      item-value="coordinates" variant="outlined">
      <template v-slot:item="{ props, item }">
        <v-list-item v-bind="props" :subtitle="item.raw.name" :title="item.raw.code"></v-list-item>
      </template>
    </v-autocomplete>

    <v-autocomplete v-model="to" @update:model-value="updateTo" :items="items" label="To" item-title="code"
      item-value="coordinates" variant="outlined">
      <template v-slot:item="{ props, item }">
        <v-list-item v-bind="props" :subtitle="item.raw.name" :title="item.raw.code"></v-list-item>
      </template>


    </v-autocomplete>
  </div>
</template>

<script>
import { store } from '@/store/store';
import * as items from '../assets/airports.json';

export default {
  name: 'SearchComponent',
  data: () => ({
    from: null,
    to: null
  }),
  props: {
    items,
  },
  methods: {
    updateFrom: (value) => { store.commit('setFrom', value) },
    updateTo: (value) => { store.commit('setTo', value) },

  }
}

</script>

